import{createVNode as _createVNode}from"vue";import{defineComponent}from'vue';import{makeNumericProp,createNamespace}from'../utils';import{useChildren}from'@vant/use';const[name,bem]=createNamespace('sidebar');export const SIDEBAR_KEY=Symbol(name);export const sidebarProps={modelValue:makeNumericProp(0)};export default defineComponent({name,props:sidebarProps,emits:['change','update:modelValue'],setup(props,{emit,slots}){const{linkChildren}=useChildren(SIDEBAR_KEY);const getActive=()=>+props.modelValue;const setActive=value=>{if(value!==getActive()){emit('update:modelValue',value);emit('change',value);}};linkChildren({getActive,setActive});return()=>_createVNode("div",{"role":"tablist","class":bem()},[slots.default?.()]);}});